import CartSection from "./Components/Restaurant/CartSection";
import ItemBox from "./Components/Restaurant/ItemBox";
import FormModal from './Components/Restaurant/FormModal';
import Header from './Components/Restaurant/Header';
import { Route, Link } from 'react-router-dom';
import { connect } from "react-redux";
import { Button, Container, Jumbotron, Col } from 'react-bootstrap';
import "./App.css";

const App = (props) => {
  let filteredItems = props.items.filter((item) => props.category === item.category);
return (
  <div className="App">
    <Route path="/" exact>
      <Container fluid className="p-0">
        <Header cart={props.cart} />
        <div className="wrapper">
          <Jumbotron className="restaurant-main">
            <label> <b>Search Filter: </b></label><br/>
            <select className="filterCategory" onChange={(e) => props.changeCategory(e.target.value)}>
                  <option defaultValue={""}> All </option>
                  {
                    props.categories.map((category) => {
                      return (
                        <option key={category} value={category} >{category}</option>
                      );
                    })
                  }
              </select><br/>
            <Button variant="primary" className="addItemBtn"
                    onClick={props.displayAddItemModal}> Add items
            </Button>
            {props.displayAddModal === true ?
              <FormModal
                type="add"
                header="Add Item"
                status={props.displayAddModal}
                categories={props.categories}
              /> : null
            }
            <Col className="itemBoxContainerMain justify-content-md-start justify-content-xl-center">
              {props.displayEditModal === true ?
                <FormModal
                  type="edit"
                  header="Edit Item"
                  status={props.displayEditModal}
                  categories={props.categories}
                /> : null
              }                 
              
                {
                props.category.length === 0 || props.category === "All" ? props.items.map((item) => {
                  return (
                    <ItemBox
                      item={item}
                      id={item.id}
                      name={item.name}
                      price={item.price}
                      image={item.image}
                      itemCategory={item.category}
                      key={item.id}
                      filteredItems={filteredItems}
                      categories={props.categories}
                      cart={props.cart}
                    />
                  );
                }) :
                filteredItems.map((item) => {
                  return (
                    <ItemBox
                      name={item.name}
                      item={item}
                      id={item.id}
                      price={item.price}
                      image={item.image}
                      itemCategory={item.category}
                      key={item.id}
                      filteredItems={filteredItems}
                      categories={props.categories}
                      cart={props.cart}
                      />
                  );
                })
                }
            </Col>
          </Jumbotron>
        </div>
      </Container>
    </Route>
    <Route path="/cart">
      <Container fluid className="p-0">
        <Header cart={props.cart} type="cart" />
          <div className="cart ">
            <CartSection
              cart={props.cart}
              total={props.total}
            />
            <Link to="/"><Button variant="secondary" className="cartBtns" 
                    > Go back
            </Button></Link>
            <Button variant="primary" className="cartBtns" onClick={props.checkOut}
                    > Checkout
            </Button>
          </div>
      </Container>
    </Route>
  </div>
  );
}

const mapStateToProps = state => {
  return {
    items: state.items,
    cart: state.cart,
    total: state.total,
    categories: state.categories,
    category: state.category,
    displayAddModal: state.displayAddModal,
    displayEditModal: state.displayEditModal
    
  }
}
  const mapDispatchToProps = dispatch => {
    return {
      changeCategory: (category) => dispatch({ type: 'CHANGE_CATEGORY', payload: category }),
      displayAddItemModal: () => dispatch ({ type: 'DISPLAY_ADD_MODAL' }),
      checkOut: () => dispatch({type: 'CHECK_OUT_ITEMS'})
    }
  }
export default connect(mapStateToProps, mapDispatchToProps)(App)