import './Header.css';
import cart from '../Restaurant/Images/cart.png';
import restaurant from '../Restaurant/Images/restaurant.png';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';

const Header = (props) => {
    return (
        <Col className="headerWrapper">
            <div className="header">
                <img className="logo" src={restaurant} alt="App Logo"></img>
                <h2 className="appName"> Resto App</h2>
            </div>
            { props.type === "cart" ? null :
                <Link to="/cart" >
                    <Button className="cartBtn" onClick={props.resetCategory}>
                        <img className="carImg" src={cart} alt="cart"></img> <span>{props.cart.length}</span>
                    </Button>
                </Link>
            }
        </Col>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        resetCategory: () => dispatch({ type: 'RESET_CATEGORY' })
    }
}

export default connect(null, mapDispatchToProps)(Header);