import { connect } from 'react-redux';
import Button from 'react-bootstrap/Button';
import './CartItems.css';

const CartItems = (props) => {

    const addQuantity = (operation) => {
        if (operation === 'minus') {
            props.item.quantity--;
        } else {
            props.item.quantity++;
        }

        if (props.item.quantity === 0) {
            let removeCartItem = props.cart.filter(item => item.id !== props.id)
            props.removeCartItem(removeCartItem)
        }
        props.changeQuantity(props.item);
    }
    
    return (
        <tr>
            <td>
                <p><strong>{props.name}</strong></p>
            </td>
            <td>
                <img className="cartItemImage" src={props.image} alt={props.name} />
            </td>
            <td>
                <Button size="sm" className="p-1" onClick={() => addQuantity('add')}> + </Button>
                <small> {props.quantity} </small> 
                <Button size="sm" className="p-1" onClick={ () => addQuantity ('minus')}> - </Button>  
            </td>
            <td>
                <p> ₱  {props.quantity * props.price} </p>
            </td>
        </tr>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        solveTotal: (solveTotal) => dispatch({ type: 'SOLVE_TOTAL', payload: solveTotal }),
        removeCartItem: (removeCartItem) => dispatch({ type: 'REMOVE_CART_ITEM', payload: removeCartItem }),
        changeQuantity: (item) => dispatch({ type: 'CHANGE_QUANTITY', payload: item})
    }
}
export default connect(null, mapDispatchToProps) (CartItems);