import CartItems from './CartItems'
import Table from 'react-bootstrap/Table'
import './CartSection.css'

const CartSection = (props) => {

  return (
    <div className="cartContainer">
      <h4>CART ITEMS:<span> ₱  {props.total}</span></h4>
      <Table striped bordered>
        <thead>
          <tr>
            <th>ITEMS</th>
            <th>IMAGE</th>
            <th>QUANTITY</th>
            <th>PRICE</th>
          </tr>
        </thead>
        <tbody>
          { props.cart.length !== 0 ?
            props.cart.map(item => {
              return <CartItems
                      item={item}
                      quantity={item.quantity}
                      name={item.name}
                      id={item.id}
                      price={item.price}
                      image={item.image}
                      addCart={props.addCart}
                      removeCartItem={props.removeCartItem}
                      key={item.name}
                      cart={props.cart}
                      disableBtn={props.disableBtn}
                      newQuantity={props.newQuantity}
                      solveTotal={props.solveTotal}
                      total={props.total}
            />
            }) : <tr className="emptyCart" >
                    <td colSpan="4" > <b>No Items</b> </td>
                </tr>
          }
        </tbody>
      </Table>
    </div>
  );
}
export default CartSection;