import { connect } from 'react-redux';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import { useState } from 'react'

const FormModal = (props) => {
    const [newName, setNewName] = useState('');
    const [newPrice, setNewPrice] = useState('');
    const [newCategory, setNewCategory] = useState('');
    const [newImage, setNewImage] = useState('');

    const addNewMenuName = (e) => {
        setNewName(e.target.value)
    }
    const addNewMenuPrice = (e) => {
        setNewPrice(e.target.value)
    }
    const addNewMenuCategory = (e) => {
        setNewCategory(e.target.value)
    }
    const addNewMenuImage = (e) => {
        setNewImage(e.target.value)
    }
    const addNewItem = () => {
        let newMenuItem = {
            name: newName,
            price: newPrice,
            category: newCategory,
            image: newImage
        }
        props.addMenuItem(newMenuItem)
        reset();
    }

    const reset = () => {
        setNewName('')
        setNewPrice('')
        setNewCategory('')
        setNewImage('')
    }

    console.log(newName);
    return (
        <Modal className="inputModal" show={props.status} onHide={props.closeModal} animation={false}>
            <Modal.Header>
                <Modal.Title>{props.header}</Modal.Title>
            </Modal.Header>
            <Modal.Body className="modalBody">
                {/* FOR INPUT NAME */}
                <label><b>Name:</b></label>
                <input type="text" placeholder="Add Name"
                    onChange = {props.type === "add" ? addNewMenuName :
                    (e) => props.editItemName(e.target.value)}
                    value = {props.type === "add" ? newName : props.editName}/> <br />
                {/* FOR INPUT PRICE */}
                <label><b>Price:</b></label>
                <input type="number" placeholder="Add price amount"
                    onChange={props.type === "add" ? addNewMenuPrice :
                    (e) => props.editItemPrice(e.target.value)}
                    value={props.type === "add" ? newPrice : props.editPrice}/> <br />
                {/* FOR INPUT CATEGORY */}
                <label><b>Category:</b></label>
                <select className="selectCategory"
                    onChange={props.type === "add" ? addNewMenuCategory :
                    (e) => props.editItemCategory(e.target.value)} value={newCategory}>
                        <option> {props.type === "add" ? null : props.editCategory} </option>
                            { props.categories.map((category) => {
                                return (
                                    <option key={category} value={category}> {category} </option>
                                    );
                                })
                            }
                </select> <br />
                {/* FOR INPUT IMAGE */}
                <label><b>image:</b></label>
                <input placeholder="Add image link"
                    onChange = {props.type === "add" ? addNewMenuImage :
                    (e) =>props.editItemImage(e.target.value)}
                    value={props.type === "add" ? newImage : props.editImage} /> <br />
                
                <Button variant="outline-primary" className="formBtn"
                    onClick={props.type === "add" ? addNewItem : () => props.addEditedItem (
                            {
                                id: props.editItem.id,
                                name: props.editName,
                                price: props.editPrice,
                                category: props.editCategory,
                                image: props.editImage
                            }
                        )}> Save
                </Button>

                <div className="modal-footer">
                    <Button type="button" className="btn btn-danger"
                        onClick={props.closeModal}> Close
                    </Button>
                </div>
            </Modal.Body>
        </Modal>
    );
}

const mapStateToProps = state => {
    return {
        editItem: state.editItem,
        editName: state.editName,
        editPrice: state.editPrice,
        editCategory: state.editCategory,
        editImage: state.editImage,
        addName: state.addName,
        addPrice: state.addPrice,
        addCategory: state.addCategory,
        addImage: state.addImage,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        editItemName: (change) => dispatch({ type: "EDIT_ITEM_NAME", payload: change }),
        editItemPrice: (change) => dispatch({ type: "EDIT_ITEM_PRICE", payload: change }),
        editItemCategory: (change) => dispatch({ type: "EDIT_ITEM_CATEGORY", payload: change }),
        editItemImage: (change) => dispatch({ type: "EDIT_ITEM_IMAGE", payload: change }),
        addMenuItem: (newMenuItem) => dispatch({ type: "ADD_MENU_ITEM", payload: newMenuItem }),
        addEditedItem: (editedItem) => dispatch({ type: 'ADD_EDITED_ITEM', payload: editedItem }),
        closeModal: () => dispatch({ type: 'CLOSE_MODAL' })
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(FormModal);