import { v4 as uuidv4 } from 'uuid';

const initialValue = {
    items: [
        {
        id: uuidv4(),
        name: "Burger",
        price: 50,
        category: "Food",
        image: "https://image.flaticon.com/icons/svg/1046/1046784.svg"
        },
        {
        id: uuidv4(),
        name: "Pizza",
        price: 100,
        category: "Food",
        image: "https://image.flaticon.com/icons/svg/1046/1046771.svg"
        },
        {
        id: uuidv4(),
        name: "Fries",
        price: 25,
        category: "Food",
        image: "https://image.flaticon.com/icons/svg/1046/1046786.svg"
        },
        {
        id: uuidv4(),
        name: "Coffee",
        price: 35,
        category: "Drink",
        image: "https://image.flaticon.com/icons/svg/1046/1046785.svg"
        },
        {
        id: uuidv4(),
        name: "Iced Tea",
        price: 45,
        category: "Drink",
        image: "https://image.flaticon.com/icons/svg/1046/1046782.svg"
        },
        {
        id: uuidv4(),
        name: "Hot Tea",
        price: 45,
        category: "Drink",
        image: "https://image.flaticon.com/icons/svg/1046/1046792.svg"
        }
    ],
    cart: [],
    total: 0,
    categories: ["Food", "Drink", "Dessert"],
    category: '',

    editItem: '',
    editId:'',
    editName:'',
    editPrice: '',
    editCategory: '',
    editImage: '',
    
    displayAddModal: false,
    displayEditModal: false
}

const solveTotal = (array) => {
    let total = 0;
    array.forEach( cartItem => {
      cartItem.subtotal = cartItem.price * cartItem.quantity 
      total += cartItem.subtotal; 
    });
    return total;
}

const appReducer = (state = initialValue, action) => {
    let total;
    switch (action.type) {
        case 'CHANGE_CATEGORY':
            return {
                ...state,
                category: action.payload
            }
        case 'ADD_TO_CART':
            let item = action.payload;
            let newCart = [...state.cart];
        
            let exists = false;
            newCart.forEach(cartItem => {
                if (cartItem.id === item.id) {
                    cartItem.quantity++;
                    exists = true;
                }
                return cartItem;
            });
        
                if (exists === false) {
                item.quantity = 1;
                newCart = [...state.cart, item];
                alert(`${item.name} successfully added`)
            }
            
            total = solveTotal(newCart);

            return {
                ...state,
                cart: newCart,
                total: total
            }
        case 'ADD_MENU_ITEM':
            if (action.payload.name.trim() === '' || action.payload.price === '' ||
                action.payload.category.trim() === '' || action.payload.image.trim() === '') {
                alert("The fields must be filled")
                return {
                    ...state
                }
            } else {
                action.payload.id = uuidv4();
                let updatedItems = [...state.items, action.payload];
                return {
                    ...state,
                    items: updatedItems,
                    displayAddModal:false
                }
            }
        case 'ADD_EDITED_ITEM':
            let findIndex = state.items.findIndex(item => item.id === action.payload.id);
            let editedMenuItems = [...state.items];
            let editedCartItems = [...state.cart];
            editedMenuItems.splice(findIndex, 1, action.payload);
            editedCartItems.forEach(cartItem => {
                if (cartItem.id === action.payload.id) {
                    cartItem.name = action.payload.name
                    cartItem.price = action.payload.price
                    cartItem.category = action.payload.category
                    cartItem.image = action.payload.image
                }
                return cartItem;
            });
            
            total = solveTotal(editedCartItems);

            return {
                ...state,
                items: editedMenuItems,
                cart: editedCartItems,
                total: total,
                displayEditModal:false
            }
        case 'REMOVE_MENU_ITEM':
            let removeItem = state.items.filter(item => item.id !== action.payload)
            let removeCartItem = state.cart.filter(item => item.id !== action.payload)
            let newAmount = solveTotal(removeCartItem);
            return {
                ...state,
                items: removeItem,
                cart: removeCartItem,
                total: newAmount
            }
        case 'REMOVE_CART_ITEM':
            return {
                ...state,
                cart:action.payload
            }
        case 'CHANGE_QUANTITY':
            let newQuantity = state.cart.map( cartItem => {
                return cartItem.id === action.payload.id ? action.payload : cartItem
            });
            let totalAmount = solveTotal(newQuantity);
            return {
                ...state,
                cart: newQuantity,
                total: totalAmount
            }
        case 'DISPLAY_ADD_MODAL':
            return {
                ...state,
                displayAddModal: true
            }
        case 'DISPLAY_EDIT_MODAL':
            return {
                ...state,
                displayEditModal: true,
                editItem: action.payload,
                editName:action.payload.name,
                editPrice: action.payload.price,
                editCategory: action.payload.category,
                editImage:action.payload.image,
            }
        case 'CLOSE_MODAL':
            return {
                ...state,
                displayAddModal: false,
                displayEditModal: false
            }
        case 'EDIT_ITEM_NAME':
            return {
                ...state,
                editName:action.payload
            }
        case 'EDIT_ITEM_PRICE':
            return {
                ...state,
                editPrice:action.payload,
            }
        case 'EDIT_ITEM_CATEGORY':
            return {
                ...state,
                editCategory:action.payload,
            }
        case 'EDIT_ITEM_IMAGE':
            return {
                ...state,
                editImage:action.payload,
            }
        case 'CHECK_OUT_ITEMS':
            alert("Try again later.");
            return {
                ...state
            }
        case 'RESET_CATEGORY':
            return {
                ...state,
                category: ''
            }
        default:
            return state
    }
}
export default appReducer;