import { connect } from 'react-redux';
import Button from 'react-bootstrap/Button';
import Col from 'react-bootstrap/Col';
import './ItemBox.css';

const ItemBox = (props) => {
    return (
        <Col sm="12" md="5" lg="5" xl="3" className="items m-2 m-md-auto m-lg-auto m-xl-1 mt-md-2 mt-lg-2">
            <div className="top">
                <img className="itemsImage" src={props.image} alt={props.name}/>
                <div className="details">
                    <p><strong> {props.name} </strong></p>
                    <p>₱ {props.price}</p>
                </div>
            </div>
            <div className="bottom">
                <Button variant = "outline-primary" className="itemsBtns"
                    onClick={() => props.addToCart(props.item)}> Add to cart
                </Button>
                <Button variant="outline-primary" className="itemsBtns"
                    onClick={() => props.displayEditModal(props.item)}> Edit
                </Button>
                <Button variant = "outline-danger" className="itemsBtns"
                    onClick={() => props.removeMenuItem(props.id)}> Delete
                </Button>
            </div>
        </Col>
    );
}

const mapDispatchToProps = dispatch => {
    return {
        addToCart: (item) => dispatch ({ type: 'ADD_TO_CART', payload: item }),
        removeMenuItem: (id) => dispatch ({ type: 'REMOVE_MENU_ITEM', payload: id }),
        displayEditModal: (item) => dispatch ({type: 'DISPLAY_EDIT_MODAL', payload: item})
    }
}
export default connect(null, mapDispatchToProps)(ItemBox);